import axios from 'axios'
import { Photos, ProfileType, UserDataType } from '../../Types/Types'
import { PFTopSegmentType } from '../Profile/ProfileInfo/ProfileDataForm/ProfileFormSegments/ProfileFormTopSegment'
import { StatusType } from '../Profile/ProfileInfo/ProfileStatus/ProfileStatus'

let instance = axios.create({
  withCredentials: true,
  baseURL: 'https://social-network.samuraijs.com/api/1.0/',
  headers: {
    //Igor
    'API-KEY': '2a15240e-71ad-4b0b-a467-a808b7479bd1',
    //nomono
    //'API-KEY': '5427931a-35dd-4342-a223-b0b3e705ed5f',
  },
});


type GetUsersResponseType = {
  items: UserDataType[]
  error: null | string
  totalCount: number
}

export type ResponseDataType<D = {}, RC = ResultCodeEnum> = {
  data: D
  fieldsErrors: []
  messages: string[]
  resultCode: RC
}

export enum ResultCodeEnum {
  Success = 0,
  Error = 1
}

export enum ResultCodeForCaptcha {
  CaptchaIsReuired = 10
}
export const userAPI = {
  getUser(currentPage = 1, pageSize = 10,
    term: string = '', friend: null | boolean = null) {
    return instance.get<GetUsersResponseType>(`users?page=${currentPage}&count=${pageSize}&term=${term}` + (friend === null ? '' : `&friend=${friend}`))
      .then(response => response.data)
  },

  follow(userId: number) {
    return instance.post<ResponseDataType>(`follow/${userId}`, {})
      .then(response => response.data)
  },

  unfollow(userId: number) {
    return instance.delete<ResponseDataType>(`follow/${userId}`)
      .then(response => response.data)
  },
}

type SavePhotoType = {
  photos: Photos
}


export const profileAPI = {
  getUserProfile(userId: number | null) {
    return instance.get<ProfileType>(`profile/` + userId)
      .then(response => response.data)

  },

  getStatus(userId: number | null) {
    return instance.get<string>(`profile/status/` + userId)
      .then(response => response.data)
  },

  updateStatus(status: StatusType) {
    return instance.put<ResponseDataType>(`profile/status/`, { status: status })
      .then(response => response.data)
  },

  savePhoto(photoFile: string | Blob) {
    const formData = new FormData()
    formData.append('image', photoFile)
    return instance.put<ResponseDataType<SavePhotoType>>('profile/photo', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    }).then(response => response.data.data.photos)
  },

  saveProfileUpdate(profile: PFTopSegmentType) {
    return instance.put<ResponseDataType>(`profile`, profile)
      .then(response => response.data)
  },
}

type AuthDataType = {
  id: number
  email: string
  login: string
}

type LoginResponseType = {
  id: number
}

export const authAPI = {
  getAuth() {
    return instance.get<ResponseDataType<AuthDataType>>(`auth/me`)
      .then(response => response.data)
  },

  signIn(formData: object) {
    return instance.post<ResponseDataType<LoginResponseType, ResultCodeEnum | ResultCodeForCaptcha>>(`auth/login`, formData)
      .then(response => response.data)
  },

  signOut() {
    return instance.delete<ResponseDataType>(`auth/login`)
      .then(response => response.data)
  },
}

type CaptchaUrlType = {
  url: string
}

export const securityApi = {
  getCaptchaUrl() {
    return instance.get<CaptchaUrlType>(`security/get-captcha-url`)
      .then(response => response.data.url)
  },
}
